<script>
import VueHotelDatepicker from '@northwalker/vue-hotel-datepicker'
import { Carousel, Slide } from "vue-carousel";

export default {
  data() {
    return {
      room: {},
      booking: {
        name: "",
        tel: "",
        date: []
      }
