  },
  components: {
    VueHotelDatepicker,
    Carousel,
    Slide
  },
  computed: {
  },
  methods: {
    addDate(date,days) {
      days = days || 0
      const newDate = new Date(Number(date));
      newDate.setDate(date.getDate() + days)
      return newDate
    },
    bgImg(url) {
      return {
        "background-position": "center center",
        "background-size": "cover",
        "background-image": "url(" + url + ")"
      };
    },
    getRoom(id) {
      const vm = this;
      const url = `https://challenge.thef2e.com/api/thef2e2019/stage6/room/${id}`;
      fetch(url, {
        method: "GET",
        headers: {
          "Authorization": "Bearer n5nxTS1Bw97SgR3dHGj6SjvW0prbVilS3D5smO4A6WsiCmj3CMQqh2RADd4B",
          "Accept": "application/json",
          "Content-Type": "application/json"
        }
      })
        .then((res) => {
          console.log(res);
          return res.json();
        })
        .then((jsonData) => {
          vm.room = jsonData.room[0];
          console.log("Room", jsonData.room[0]);
        })
        .catch((err) => {
          console.log("error", err);
        });
    },
    createOrder(id) {
      const vm = this;
      const booking = vm.booking;
      console.log("ID", id);
      console.log("Booking", booking);
      const url = `https://challenge.thef2e.com/api/thef2e2019/stage6/room/${id}`;
      this.$validator.validate().then(result => {
        if (result) {
          fetch(url, {
        method: "POST",
        body: JSON.stringify(booking),
        headers: {
          "Authorization": "Bearer n5nxTS1Bw97SgR3dHGj6SjvW0prbVilS3D5smO4A6WsiCmj3CMQqh2RADd4B",
          "Accept": "application/json",
          "Content-Type": "application/json"
        }
      })
        .then((res) => {
          console.log(res);
          return res.json();
        })
        .then((jsonData) => {
          console.log("POST", jsonData);
        })
        .catch((err) => {
          console.log("error", err);
        });
        }
      })
    },
    deleteOrder() {
      const vm = this;
      const url = `https://challenge.thef2e.com/api/thef2e2019/stage6/rooms`;
      fetch(url, {
        method: "DELETE",
        headers: {
          Authorization:
            "Bearer n5nxTS1Bw97SgR3dHGj6SjvW0prbVilS3D5smO4A6WsiCmj3CMQqh2RADd4B",
          Accept: "application/json"
        }
      })
        .then(res => {
          console.log(res);
          return res.json();
        })
        .then(jsonData => {
          console.log("Delete", jsonData);
        })
        .catch(err => {
          console.log("error", err);
        });
    }
  },
  created() {
    const id = this.$route.params.id;
    this.getRoom(id);
    // this.createOrder(id)
  }
};
